'use strict';
import iziToast from 'izitoast';
import 'izitoast/dist/css/iziToast.min.css';
import SimpleLightbox from 'simplelightbox';
import 'simplelightbox/dist/simple-lightbox.min.css';

import { fetchImages } from './js/pixabay-api.js';
import { displayImages } from './js/render-functions.js';

const inputSearch = document.querySelector('.input-picstyle');
const searchButton = document.querySelector('.search-btn');
const gallery = document.querySelector('.gallery');

searchButton.addEventListener('click', () => {
  const query = inputSearch.value.trim().split(' ').join('+');

  gallery.innerHTML = '<span class="loader"></span>';
  fetchImages(query)
    .then(data => {
      const images = data.hits;
      if (images.length === 0) {
        iziToast.error({
          color: 'yellow',
          message:
            'Sorry, there are no images matching your search query. Please try again!',
          position: 'center',
          progressBarColor: 'rgb(0, 255, 184)',
          timeout: 2000,
        });
      }
      displayImages(images);
      let galleryBox = new SimpleLightbox('.pic-card a', {
        captionsData: 'alt',
        captionDelay: 250,
      });
      galleryBox.refresh();
    })
    .catch(error => {
      alert('Error while fetching images');
    })
    .finally(() => {
      inputSearch.value = '';
      let lastQuery = query.split('+').join(' ');
      inputSearch.placeholder = `Last searched for "${lastQuery}"`;
    });
});
